import axios from 'axios'

const devURL = 'http://localhost:4000'
const productionURL = 'https://alkemy-back-challenge.herokuapp.com'
const baseURL = `${devURL}/api/users/login`

export const getUser = async (credentials) => {
    try{
    const {data} = await axios.post(baseURL, credentials)
    console.log(data)
    return data
    }
    catch(error){
        console.log(error)
    }
}